.cover-modal {
    background-color: #f4f5f7;
    position: relative;
    left: 100%;
    transform: translate(-40.8%, 0);
    border-radius: 2px;
    border: 1px solid rgba(207, 191, 191, 0.6);
    width: 350px;
    white-space: nowrap;
    overflow: auto;
    transition: 0.6s;
    z-index: 10;
    top: 193px;
    color: #172b4d;
    min-height: 180px;

    .cover-modal-header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-bottom: 1px solid black;
        height: 60px;
        > :first-child {
            margin-left: auto;
            margin-right: auto;
        }
        button {
            background-color: inherit;
            position: absolute;
            top: 0;
            right: 0;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 60px;
            height: 60px;
        }
    }
    .cover-modal-body {
        padding: 0 12px 12px;
        .cover-color-container {
            width: 100%;
            display: grid;
            align-items: center;
            grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
            gap: 8px;
            padding: 4px 0;
            overflow-x: hidden;
            .cover-color {
                margin: 0;
                border-radius: 3px;
                height: 72px;
                width: 100%;
                background-size: cover;
                background-repeat: no-repeat;
                background-position: center;
                box-shadow: none;
                width: 57px;
                height: 32px;
                cursor: pointer;
            }
        }
        .cover-attachments-container {
            width: 100%;
            display: grid;
            align-items: center;
            grid-template-columns: 1fr 1fr 1fr;
            gap: 15px 28px;
            padding: 4px 0;
            overflow-x: hidden;
            .cover-attach {
                background-position: 50%;
                background-size: contain;
                background-repeat: no-repeat;
                border-radius: 3px;
                background-color: rgb(253, 177, 78);
                height: 48px;
                max-width: 89px;
                border-radius: 3px;
                cursor: pointer;
            }
        }
        .add-attachment-cover-btn {
            width: 100%;
            margin: 7px 0;
        }
        .remove-cover-btn {
            width: 100%;
        }
    }
}
